import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchOrder } from '../../actions/searchAction';

import { CustomStyles, CustomStylesDesignType_5 } from '../../CustomMaterialUIStyles/FilterComponents';
// import {Filters} from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
// import ArrowDropRight from 'material-ui/svg-icons/navigation-arrow-drop-right';

// const styles = { 
// };

class ImgOrder extends Component {
  render() {
    let _styles = {};
   
    const booleanCheck = (x) => {
      if (this.props.order === x) {
        return true
      } else {
        return false
      }
    };

    if (this.props.designType === 5) {
      _styles = CustomStylesDesignType_5
    }
    
    return (
      <SelectField
        name='order'
        className='SelectField'
        value={this.props.order}
        onChange={(e, index, value) => { this.props.fetchOrder(value); this.props.fetchImages() }}
        // onChange={(e, index, value) => { this.props.fetchOrder(value); this.props.fetchImages() }}
        // floatingLabelText='Sort by'
        style={Object.assign({}, CustomStyles.rootSelectField, _styles.rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, _styles.labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, _styles.iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, _styles.dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
        // menuStyle={styles.selectFieldMenuItem}
        // menuItemStyle={styles.selectFieldMenuItem}
      >
        <MenuItem 
          className='filtersMenuItem'
          value='popular'
          label={<span>order by: <em> popular </em> </span>} 
          primaryText='popular' 
          checked={booleanCheck('popular')}
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)} 
        />
        <MenuItem 
          className='filtersMenuItem'
          value='latest' 
          label={<span>order by: <em> latest </em> </span>}
          primaryText='latest'
          checked={booleanCheck('latest')}
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)} 
        />
      </SelectField>
    )
  }
}

ImgOrder.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchOrder: PropTypes.func.isRequired,
  order: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  order: state.search.order,
})

export default connect(mapStateToProps, { fetchImages, fetchOrder })(ImgOrder)


