import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchColor } from '../../actions/searchAction';

import { CustomStyles, CustomStylesDesignType_5 } from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

// const styles = {
// };

class ImgColor extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.selectionRenderer = this.selectionRenderer.bind(this);
  }

  handleChange(e, index, val) {
    if (val.length === 0) {
      this.props.fetchColor([null])
    } else {  
      switch (true) {
        case ((val[0] === null) && (!(val[1] === 'undefined'))):
          this.props.fetchColor([val[1]])
          break;
        case (val[val.length - 1] === null):
          this.props.fetchColor([null])
          break;
        default:
          this.props.fetchColor(val)
      }
      this.props.fetchImages()
    }
  }

  selectionRenderer = (values) => {
    switch (values.length) {
      case 0:
        return '';
      case 1:
        if(values[0]===null) {
          return (<span>color: <em>all</em></span>)
        }
        return (<span>color: <em>{values[0]}</em></span>);
      default:
        return (<span>color: <em>{values.join(', ')}</em></span>);
    }
  }

  render() {
    const colorArray = ["grayscale", "transparent", "red", "orange", "yellow", "green",
    "turquoise", "blue", "lilac", "pink", "white", "gray", "black", "brown"];

    let _styles = {};

    const booleanCheck = (x) => {
      if (this.props.color && (this.props.color.indexOf(x) > -1)) {
        return true
      } else {
        return false
      }
    } ;

    if (this.props.designType === 5) {
      _styles = CustomStylesDesignType_5
    }

    const MenuItemColor = (
      colorArray.map((val) => (
        // <div className='filtersMenuItem'>
          <MenuItem
            className='filtersMenuItem'
            key={val}
            value={val}
            primaryText={val}
            checked={booleanCheck(val)}
            style={CustomStyles.selectFieldRootMenuItem}
            innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}
          />
        // </div>
      ))
    );

    return (
      <SelectField
        name='color'
        className='SelectField'
        autoWidth={true}
        // value={(this.props.value === '') ? 'reset' : this.props.value}
        value={this.props.color}
        onChange={this.handleChange}
        multiple={true}
        maxHeight={300}
        selectionRenderer={this.selectionRenderer}
        style={Object.assign({}, CustomStyles.rootSelectField, _styles.rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, _styles.labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, _styles.iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, _styles.dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
        // fullWidth={true}
      >
        {/* <div className='filtersMenuItem'> */}
          <MenuItem 
            className='filtersMenuItem'
            value={null} 
            // label={(<span>category: <em> all </em> </span>)}
            // label='all colors' 
            primaryText='ALL'
            checked={booleanCheck(null)}
            style={Object.assign({}, CustomStyles.selectFieldRootMenuItem, {})}
            innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)} 
          />
        {/* </div> */}
        <Divider inset={true} style={CustomStyles.menuDivider}/>
        {MenuItemColor  }
      </SelectField>
    )
  }
}

ImgColor.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchColor: PropTypes.func.isRequired,
  color: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  color: state.search.color,
})

export default connect(mapStateToProps, { fetchImages, fetchColor })(ImgColor)


