import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import ImgOrientation from './SearchFilters/ImgOrientation';
import ImgCategory from './SearchFilters/ImgCategory';
import ImgColor from './SearchFilters/ImgColor';
// import ImgQty from './SearchFilters/ImgQty';
import ImgType from './SearchFilters/ImgType';
import ImgOrder from './SearchFilters/ImgOrder';
// import ColorChip from './SearchFilters/Chip/ColorChip';

import { CustomStyles } from '../CustomMaterialUIStyles/FilterComponents';

import Divider from 'material-ui/Divider';
// import { blueGrey50, white } from 'material-ui/styles/colors';

class SearchFilters extends Component {
  render() {
    // const styles = {
    //   rootComponent: {
    //     display: 'flex',
    //     flexWrap: 'nowrap',
    //     justifyContent: 'space-around',
    //     // position: 'relative',
    //     // top: 57,
    //     backgroundColor: white,
    //     width: '100%',
    //   },
    // }
    
    let styles = {
      _filtersBar: {
        flexFlow: 'column',
        justifyContent: 'space-evenly',
      },
      _filtersBarItem: {
        maxWidth: '100%',
      }
    }

    if (!(this.props.designType === 5)) {
      styles._filtersBar.flexFlow = 'row';
      styles._filtersBarItem.maxWidth = '20%'
    }

    styles._filtersBar.justifyContent = (this.props.designType < 3) ? 'space-evenly' : 'space-around';

    return (
      <div>
        <div className='filtersBar' style={Object.assign({}, CustomStyles.filtersBar, styles._filtersBar)}>
          {/* <ImgQty /> */}
          <div style={styles._filtersBarItem} > <ImgOrder designType={this.props.designType} /> </div> 
          <div style={styles._filtersBarItem} > <ImgOrientation designType={this.props.designType} /> </div>
          <div style={styles._filtersBarItem} > <ImgCategory designType={this.props.designType} /> </div>
          <div style={styles._filtersBarItem} > <ImgColor designType={this.props.designType} /> </div>
          <div style={styles._filtersBarItem} > <ImgType designType={this.props.designType} /> </div>
          {/* <ColorChip 
            value={this.props.color}
            onChange={(value) => { this.props.fetchAmount(value); this.props.fetchImages()}}
          /> */}
        </div>
        <Divider />
      </div>

    )
  }
}

SearchFilters.propTypes = {
  designType: PropTypes.number.isRequired,
}

const mapStateToProps = state => ({
  designType: state.search.designType,
})

export default connect(mapStateToProps, {})(SearchFilters)


