import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchOrientation } from '../../actions/searchAction';

import { CustomStyles, CustomStylesDesignType_5 } from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

  // const styles = {
  // };

class ImgOrientation extends Component {
  render() {
    let _styles = {};

    const booleanCheck = (x) => {
      if (this.props.orientation === x) {
        return true
      } else {
        return false
      }
    };

    if (this.props.designType === 5) {
      _styles = CustomStylesDesignType_5
    }

    return (
      <SelectField
        name='orientation'
        className='SelectField'
        value={this.props.orientation}
        onChange={(e, index, value) => { this.props.fetchOrientation(value); this.props.fetchImages() }}
        // floatingLabelText='Orientation'
        style={Object.assign({}, CustomStyles.rootSelectField, _styles.rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, _styles.labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, _styles.iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, _styles.dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
      >
        <MenuItem 
          className='filtersMenuItem'
          value='all'
          label={(<span>orientation: <em> all </em> </span>)}
          primaryText='ALL' 
          checked={booleanCheck('all')}
          style={Object.assign({}, CustomStyles.selectFieldRootMenuItem, {})}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}
        />
        <Divider inset={true} style={CustomStyles.menuDivider} />
        <MenuItem 
          className='filtersMenuItem'
          value='horizontal'
          label={(<span>orientation: <em> landscape </em> </span>)}
          primaryText='landscape'
          checked={booleanCheck('horizontal')}
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}
        />
        <MenuItem 
          className='filtersMenuItem'
          value='vertical' 
          label={(<span>orientation: <em> portrait </em> </span>)}
          primaryText='portrait' 
          checked={booleanCheck('vertical')}
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}          
        />
      </SelectField>
    )
  }
}

ImgOrientation.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchOrientation: PropTypes.func.isRequired,
  orientation: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  orientation: state.search.orientation,
})

export default connect(mapStateToProps, { fetchImages, fetchOrientation })(ImgOrientation)


