import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { fetchImages } from '../../actions/searchAction';
import { fetchCategory } from '../../actions/searchAction';

import { CustomStyles, CustomStylesDesignType_5 } from '../../CustomMaterialUIStyles/FilterComponents';

import SelectField from 'material-ui/SelectField';
import MenuItem from 'material-ui/MenuItem';
import Divider from 'material-ui/Divider';

// const styles = {
// };

class ImgCategory extends Component {
  render() {
    const categoryArray = ['fashion', 'nature', 'backgrounds', 'science', 'education', 'people',
    'feelings', 'religion', 'health', 'places', 'animals', 'industry', 'food', 'computer', 
    'sports', 'transportation', 'travel', 'buildings', 'business', 'music'];

    let _styles = {};

    const booleanCheck = (x) => {
      if (this.props.category === x) {
        return true
      } else {
        return false
      }
    };

    if (this.props.designType === 5) {
      _styles = CustomStylesDesignType_5
    }

    const MenuItemCategory = (
      categoryArray.map((val, id) => (
        <MenuItem
          className='filtersMenuItem'
          key={val + id}
          value={val}
          label={<span>category: <em> {val} </em> </span>}
          primaryText={val}
          checked={booleanCheck(val)}
          style={CustomStyles.selectFieldRootMenuItem}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}
        />
      ))
    );

    return (
      <SelectField
        name='category'
        className='SelectField'
        autoWidth={true}
        value={this.props.category}
        onChange={(e, index, value) => { this.props.fetchCategory(value); this.props.fetchImages() }}
        maxHeight={300}
        // floatingLabelText='Category'
        style={Object.assign({}, CustomStyles.rootSelectField, _styles.rootSelectField)}
        labelStyle={Object.assign({}, CustomStyles.labelSelectField, _styles.labelSelectField)}
        iconStyle={Object.assign({}, CustomStyles.iconSelectField, _styles.iconSelectField)}
        underlineStyle={CustomStyles.underlineSelectField}
        dropDownMenuProps={Object.assign({}, CustomStyles.dropDownMenuProps, _styles.dropDownMenuStyle)}
        listStyle={CustomStyles.selectFieldList}
      >
        <MenuItem
          className='filtersMenuItem'
          value=''
          label={<span>category: <em> all </em> </span>}
          primaryText='ALL' 
          checked={booleanCheck('')}
          style={Object.assign({}, CustomStyles.selectFieldRootMenuItem, {})}
          innerDivStyle={Object.assign({}, CustomStyles.selectFieldMenuItem, _styles.selectFieldMenuItem)}
        />
        <Divider inset={true} style={CustomStyles.menuDivider} />
        {MenuItemCategory}
      </SelectField>
    )
  }
}

ImgCategory.propTypes = {
  fetchImages: PropTypes.func.isRequired,
  fetchCategory: PropTypes.func.isRequired,
  category: PropTypes.string.isRequired,
}

const mapStateToProps = state => ({
  category: state.search.category,
})

export default connect(mapStateToProps, { fetchImages, fetchCategory })(ImgCategory)


